                    </v-layout>
                </v-flex>

                <v-flex xs12 md12 sm12 v-if="title">
                    <v-chip label outline v-model="file" close v-bind:color="color" v-bind:text-color="color">
                        <v-avatar v-if="check">
                            <v-icon>check_circle</v-icon>
                        </v-avatar>
                        {{file.name}}
                    </v-chip>
                </v-flex>
                <v-layout v-if="arquivo.titulo" row wrap>
                    <v-flex xs12 md12 sm12>
                        <v-layout row wrap>
                            <upload-btn color="blue-grey" class="white--text" :fileChangedCallback="fileChanged" title="Adcionar arquivo">
                                <template slot="icon">
                                    <v-icon right dark>archive</v-icon>
                                </template>
                            </upload-btn>

                            <v-btn :loading="loading3" v-if="file.name && !check" :disabled="loading3" color="blue-grey" class="white--text" @click="teste()">
                                Upload
                                <v-icon right dark>cloud_upload</v-icon>
                            </v-btn>
                        </v-layout>
                    </v-flex>
                    <v-flex xs12 md12 sm12 v-if="loader">
                        <v-flex xs12 md12 sm12>
                            <v-progress-linear color="teal" v-model="uploadPercentage"></v-progress-linear>
                        </v-flex>
                    </v-flex>
                </v-layout>
            </v-card>
        </v-flex>

        <v-flex sm12>
            <h3>Arquivos enviados</h3>
        </v-flex>

        <v-flex xs12 sm12 md12>
            <v-card>
                <v-card-title>
                    Nutrition
                    <v-spacer></v-spacer>
                </v-card-title>
                <v-data-table dark hide-actions="true" :headers="headers" :items="filers">
                    <template slot="items" slot-scope="props">
                        <td>{{ props.item.tipo }}</td>
                        <td>{{ props.item.title }}</td>
                        <td>{{ props.item.complemento }}</td>
                        <td>{{ props.item.createDate }}</td>
                        <td>
                            <v-icon>attach_file</v-icon>
                        </td>
                    </template>
                </v-data-table>
