<script>
import services from "../services/userService";
import UploadButton from 'vuetify-upload-button';
import jsonFormData from 'object-to-formdata';
import {
    http
} from '../services/config'
export default {

    data() {
        return {
            valid: false,
            user: null,
            filers: null,
            arquivo: {
                titulo: '',
                complemento: '',
                tipo: '',
                desc: ''
            },
            headers: [{
                    text: 'Tipo',
                    align: 'left',
                    sortable: false,
                    value: 'tipo'
                },
                {
                    text: 'Titulo',
                    value: 'titulo'
                },
                {
                    text: 'Complemento',
                    value: 'complemento'
                },
                {
                    text: 'Data de publicação',
                    value: 'data'
                },
            ],
            uploadPercentage: 0,
            title: '',
            loader: null,
            loading: false,
            check: false,
            color: '',
            file: {},
            rota: 'http://191.252.200.167:3000/file/send',
            url: '',
            files: [],
            tipoArquivos: [{
                    tipo: 'CADASTRO DE FUNCIONÁRIO',
                    desc: 'ANEXAR ARQUIVOS: COPIA DA CTPS, RG, CPF, COMPROVANTE DE RESIDÊNCIA, TITULO, CERTIDÃO DE CASAMENTO, CERTIDÃO DE NASCIMENTO DOS DEPENDENTES, EXAME ADMISSIONAL' + ' ' +
                        'RECEPÇÃO DE DOCUMENTOS: CONTRATO DE EXPERIENCIA, ETIQUETAS DA CTPS, TERMO DE RESPONSABILIDADE, RECIBO DE ENTREGA DA CTPS'
                },
                {
                    tipo: 'FOLHA',
                    desc: 'ANEXAR ARQUIVOS: FALTAS, HORAS EXTRAS, ADICIONAIS E DESCONTOS' + ' ' +
                        'RECEPÇÃO DE DOCUMENTOS: FOLHA DE PAGAMENTO, CONTRA-CHEQUE, GPS, GRF, TAXA SINDICAL, DARF,'
                },
                {
                    tipo: 'FÉRIAS',
                    desc: 'ANEXAR ARQUIVOS' + " " +
                        'RECEPÇÃO DE DOCUMENTOS: PREVISÃO DE FÉRIAS, AVISO DE FÉRIAS, RECIBO DE FÉRIAS, FÉRIAS COLETIVA'
                },
                {
                    tipo: 'RESCISÃO',
                    desc: 'ANEXAR ARQUIVOS' + ' ' +
                        'RECEPÇÃO DE DOCUMENTOS: TRCT, SIMULAÇÃO RESCISÃO, RESCISÃO COLETIVA, SALÁRIO CONTRIBUIÇÃO, AVISO PREVIO/DISPENSA'
                },
                {
                    tipo: 'AFASTAMENTO',
                    desc: 'ANEXAR ARQUIVOS:  LAUDO MÉDICO, ATESTADO MÉDICO RECEPÇÃO DE DOCUMENTOS'
                },
                {
                    tipo: 'DÉCIMO TERCEIRO',
                    desc: 'ANEXAR ARQUIVOS' + ' ' +
                        'RECEPÇÃO DE DOCUMENTOS: FOLHA PRIMEIRA PARCELA, FOLHA SEGUNDA PARCELA, GPS 13º, CONTRA-CHEQUE PRIMEIRA PARCELA, CONTRA-CHEQUE SEGUNDA PARCELA'
                },
                {
                    tipo: 'NOTAS FISCAIS',
                    desc: 'ANEXAR ARQUIVOS: XML, ARQUIVO SPED,  INVENTÁRIO RECEPÇÃO DE DOCUMENTOS: FATURAMENTO, INVENTÁRIO'
                },
                {
                    tipo: 'ARQUIVOS MAGNÉTICOS',
                    desc: 'ANEXAR ARQUIVOS: SPED ICMS, EFD CONTRIBUIÇÕES RECEPÇÃO DE DOCUMENTOS'
                },
                {
                    tipo: 'ICMS e IPI',
                    desc: 'ANEXAR ARQUIVOS: RECEPÇÃO DE DOCUMENTOS: APURAÇÃO E SALDO DE ICMS, GUIAS ESTADUAIS, DARF IPI, LIVROS FISCAIS'
                },
                {
                    tipo: 'FEDERAL',
                    desc: 'ANEXAR ARQUIVOS: ARQUIVOS SPED CONTRIBUIÇÕES, SPED ICMS, SPED REINF, RESUMO DE TRIBUTOS, DIRF CARTÃO DE CRÉDITO RECEPÇÃO DE ARQUIVOS: DAS, DAS PARCELAMENTO SIMPLES NACIONAL, DARF PIS, DARF COFINS, DARF CSLL, DARF IRPJ, DARF RETENÇÕES, DEFIS/DASN'
                },
                {
                    tipo: 'MUNICIPAL',
                    desc: 'ANEXAR ARQUIVOS: NOTAS DE SERVIÇOS, RELATÓRIOS DE RETENÇÃO DE SERVIÇO' + ' ' +
                        'RECEPÇÃO DE DOCUMENTOS: LIVROS FISCAIS, ISS, ISS UNIPROFISSIONAL, GUIAS MUNICIPAIS'
                },
                {
                    tipo: 'ESCRITA CONTÁBIL',
                    desc: 'ANEXAR ARQUIVOS: TXT MOVIMENTAÇÃO BANCÁRIA, LIVRO CAIXA, CONTRATOS DE FINANCIAMENTO, CONTRATO DE EMPRESTIMO, CONTRATO ARRENDAMENTO, ITENS IMOBILIZADOS, ESCRITA DE IMOVEIS' + ' ' +
                        'RECEPÇÃO DE ARQUIVOS: LALUR, BALANÇO PATRIMONIAL, LIVRO CAIXA, ECF e FCONT, BALANCETE, RAZÃO, DIÁRIO, DEMONSTRAÇÃO DE RESULTADO DO EXERCICIO, ANÁLISE POR CENTRO DE CUSTO'
                }
            ]
        }
    },

    components: {
        'upload-btn': UploadButton
    },
    methods: {
        fileChanged(file) {
            this.color = 'blue-grey',
                this.file = file;
            this.title = file.name;
            this.check = false;
            this.uploadPercentage = 0;
        },
        resetArquivo() {
            this.arquivo.titulo = ''
            this.arquivo.complemento = ''
        },
        listarArquivos(id) {
            services.getFiles(id).then(r => {
                if (r.status == 200) {
                    var arquivos = [];
                    var arquivo = {};
                    for (let index = 0; index < r.data.length; index++) {
                        arquivo = r.data[index];
                        arquivo.createDate = this.formatDate(r.data[index].createDate);
                        arquivos.push(arquivo);
                    }
                    this.filers = arquivos;
                }
            })
        },
        formatDate(date) {
            var mDate = new Date().toISOString().
            replace(/T/, ' '). // replace T with a space
            replace(/\..+/, '');
            if (!mDate) return null
            const [year, month, day] = mDate.split('-')
            const [mDay, horas] = day.split(' ')
            return `${mDay}/${month}/${year} - ${horas}`
        },
        teste() {
            this.loader = 'loading3';
            const l = this.loader
            this[l] = !this[l]
            this.arquivo.idUser = this.user.data.id;
            let formData = new FormData();
            formData.append('arquivo', JSON.stringify(this.arquivo));
            formData.append('sampleFile', this.file);
            this.fileUpoad(formData).then(r => {
                this[l] = false;
                this.loader = null;
                this.uploadPercentage = 0;
                this.color = 'green';
                this.check = true;
                this.listarArquivos(this.user.data.id);
                setTimeout(() => (this.resetArquivo()), 1000)
            }).catch(function () {

            });
        },
        fileUpoad(data) {
            return http.post('file/send', data, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                },
                onUploadProgress: function (progressEvent) {
                    this.uploadPercentage = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total));
                }.bind(this)
            });
        },
    },
    mounted() {
        this.user = this.user = JSON.parse(this.$cookie.get("user"));
        this.listarArquivos(this.user.data.id);
    },
    computed: {
        mUrl() {
            if (this.user != null) {
                return this.url = this.rota + '/' + this.title + '/' + this.user.data.id;
            }
        }
    }
}
</script>

